import React from 'react';
import GoogleButton from 'react-google-button'
import firebase from './firebaseApp';
import 'firebase/firestore';
import 'firebase/auth';
import { Link } from 'react-router-dom';

import {useAuthState} from 'react-firebase-hooks/auth';






function Header() {

  const auth = firebase.auth();

const [user]=useAuthState(auth);


// sign in function with google


  const signInWithGoogle = () => {
    const provider = new firebase.auth.GoogleAuthProvider();
    auth.signInWithPopup(provider).then(data=>{
      localStorage.setItem("email",data.user.email)
    

    });
  
  }

  const logOut = () => {
    auth.signOut().then(()=> {
      localStorage.removeItem("email");
      console.log('logged out')
    }).catch((error) => {
      console.log(error.message)
    })
  }




  
  
  return (
    <>
    <header>
      
    <div id="menu" className="menu">

    {user ?<> <Link to="/" className="link" style={{marginRight:"8px" ,cursor:"pointer",color: 'inherit', textDecoration: 'inherit',fontSize:"20px", borderRight: '1px solid black'}} >Home</Link><Link to="/saveditems" className="link" style={{cursor:"pointer",color: 'inherit', textDecoration: 'inherit',fontSize:"20px", borderRightWidth: '1px' , borderRightColor:'black', borderRightStyle:'solid'}} >SavedItems</Link> </>: ""}

    {user ?<><button className="blue" style={{cursor:"pointer"}} onClick={logOut}>Logout</button> </>: <GoogleButton
onClick={ signInWithGoogle }
/>}
    

    </div>
   
    <div className ="wrapper">
      <div className="headerContainer">
        <h1>Makeup Finder.</h1>
        <p>This website is designed to help you find makeup products.</p>
      </div>
    </div>

  </header>

 
  </>
  )
}

export default Header
